.App {
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
}
section {
  width: 100vw;
  height: 100vh;
}

.contenedorIconoMenu {
  z-index: 2 ;
  position: fixed;
  top: 30px;
  right: 100px;
  width: 10px;
  height: 10px;
  cursor: pointer;
}
.iconoMenu{
  transition: all .5s ease-in-out;
  width: 90px;
  height: 90px;
}
.abrirMenu{
  width: 100vw !important;
}
.slideBlanco{
  height: 35vh;
}
.slideNegro{
  background-color: black;
  height: 30vh;
}
@media screen and (max-width:480px) {
  .contenedorIconoMenu{
    -webkit-tap-highlight-color: transparent;
    top:18px;
    right: 20%;
  }
  .iconoMenu{
    width: 70px;
    height: 70px;
  }
  .contenedorIconoMenu:hover .iconoMenu{
    width: 80px;
    height: 80px;
  }
}